import axios from 'axios'

const apiListLead = "/list-lead-conversion"
const apiListCountry = "/get-countries"
const apiListLevel = "/get-all-levels"
const apiListSubjectArea = "/get-all-subject-areas"
const apiListUniversityByCountry = "/get-universities-by-country"
const apipostCreateLead = "/post-create-lead-conversion"
export default {
    getLeadConversion (data) {
        return axios.get(apiListLead, {params: data});
    },
    getCountries () {
        return axios.get(apiListCountry);
    },
    getUniversityByCountries (data) {
        return axios.get(apiListUniversityByCountry, {params: data});
    },
    getLevel () {
        return axios.get(apiListLevel);
    },
    getSubjectArea () {
        return axios.get(apiListSubjectArea);
    },
    postCreateLead (data) {
        return axios.post(apipostCreateLead, data);
    }
}
