<template>
    <div>
        <div class="row">
            <div class="col-sm-4 col-3">
                <h4 class="page-title">{{this.pageTitle}}</h4>
            </div>
        </div>
        <div class="box-content box box-primary">
            <div class="box-body box-form-content">
                <el-form :model="partner" :rules="rules" ref="partnerForm" v-loading.body="loading"
                         label-width="120px"
                         label-position="top"
                         class="demo-ruleForm">
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Name Partner" prop="title">
                                <el-input v-model="partner.title"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Branch" prop="branch_id">
                                <el-select v-model="partner.branch_id" placeholder="Select branch">
                                    <el-option
                                            v-for="item in branches"
                                            :key="item.id"
                                            :label="item.title"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12">
                            <el-form-item label="Status" prop="status">
                                <el-radio-group v-model="partner.status">
                                    <el-radio :label="0" border>Hidden</el-radio>
                                    <el-radio :label="1" border>Show</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </div>
                    </div>
                    <el-form-item>
                        <el-button type="success" round @click="submitForm('partnerForm')">Save</el-button>
                        <el-button type="info" round @click="cancel()">Cancel</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>
<script>
    import {RepositoryFactory} from '../../../repositories/RepositoryFactory'

    const partnerRe = RepositoryFactory.get('partner');
    const branch = RepositoryFactory.get('branch');
    export default {
        data () {
            return {
                partner: {
                    title: '',
                    status: 0,
                    branch_id: ''
                },
                branches: [],
                loading: false,
                pageTitle: 'Create Partner',
                rules: {
                    title: [
                        {required: true, message: 'Please enter name partner', trigger: 'blur'}
                    ],
                    branch_id: [
                        {required: true, message: 'Please choose branch', trigger: 'change'}
                    ]
                }
            };
        },
        methods: {
            submitForm (formName) {
                this.$refs[formName].validate((valid) => {
                    if(valid) {
                        this.loading = true;
                        if(this.$route.params.partnerId === undefined) {
                            partnerRe.postCreatePartner(this.partner).then((response) => {
                                this.loading = false;
                                this.$message({
                                    type: 'success',
                                    message: response.data.data.message,
                                });
                                return this.$router.push({name: 'Partner'})
                            });
                        } else {
                            partnerRe.putEditPartner(this.$route.params.partnerId, this.partner).then((response) => {
                                this.loading = false;
                                this.$message({
                                    type: 'success',
                                    message: response.data.data.message,
                                });
                                return this.$router.push({name: 'Partner'})
                            });
                        }
                    } else {
                        return false;
                    }
                });
            },
            cancel () {
                return this.$router.push({name: 'Partner'})
            },
            fetchPartner () {
                partnerRe.findPartner({partnerId: this.$route.params.partnerId}).then((response) => {
                    this.partner = response.data.data;
                });
            },
            getBranches () {
                branch.getBranches().then((response) => {
                    this.branches = response.data.data;
                });
            },
        },
        mounted () {
            this.getBranches();
            if(this.$route.params.partnerId !== undefined) {
                this.pageTitle = 'Edit Partner'
                this.fetchPartner();
            }
        }
    }
</script>
