<template>
    <div>
        <div class="row">
            <div class="col-sm-4 col-3">
                <h4 class="page-title">{{this.pageTitle}}</h4>
            </div>
        </div>
        <div class="box-content box box-primary">
            <div class="box-body box-form-content">
                <el-form :model="eventManagement" :rules="rules" ref="eventManagementForm" v-loading.body="loading"
                         label-width="120px"
                         label-position="top"
                         class="demo-ruleForm">
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Name Event" prop="title">
                                <el-input v-model="eventManagement.title"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Type Event" prop="type_id">
                                <el-select v-model="eventManagement.type_id" placeholder="Select type event">
                                    <el-option
                                            v-for="item in typeEvents"
                                            :key="item.id"
                                            :label="item.title"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Branch" prop="branch_id">
                                <el-select v-model="eventManagement.branch_id" placeholder="Select branch">
                                    <el-option
                                            v-for="item in branches"
                                            :key="item.id"
                                            :label="item.title"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12">
                            <el-form-item label="Event Start Date" prop="event_start_date">
                                <el-date-picker
                                        v-model="eventManagement.event_start_date"
                                        type="datetime"
                                        format="dd-MM-yyyy HH:mm"
                                        :value="eventManagement.event_start_date"
                                        placeholder="Select date and time">
                                </el-date-picker>
                            </el-form-item>
                        </div>
                        <div class="col-md-12">
                            <el-form-item label="Event Event Date" prop="event_end_date">
                                <el-date-picker
                                        v-model="eventManagement.event_end_date"
                                        :value="eventManagement.event_end_date"
                                        format="dd-MM-yyyy HH:mm"
                                        type="datetime"
                                        placeholder="Select date and time">
                                </el-date-picker>
                            </el-form-item>
                        </div>
                    </div>
                    <el-form-item>
                        <el-button type="success" round @click="submitForm('eventManagementForm')">Save</el-button>
                        <el-button type="info" round @click="cancel()">Cancel</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>
<script>
    import {RepositoryFactory} from '../../../repositories/RepositoryFactory'
    import helpers from '../../../helpers'

    const EventRepository = RepositoryFactory.get('EventRepository');
    const branch = RepositoryFactory.get('branch');
    export default {
        data () {
            return {
                eventManagement: {
                    title: '',
                    event_end_date: '',
                    event_start_date: '',
                    type_id: '',
                    branch_id: ''
                },
                typeEvents: [],
                branches: [],
                loading: false,
                pageTitle: 'Create Event',
                rules: {
                    title: [
                        {required: true, message: 'Please enter name event', trigger: 'blur'}
                    ],
                    type_id: [
                        {required: true, message: 'Please select type event', trigger: 'change'}
                    ],
                    branch_id: [
                        {required: true, message: 'Please select branch', trigger: 'change'}
                    ],
                    event_end_date: [
                        {type: 'date', required: true, message: 'Please pick a end date', trigger: 'change'}
                    ],
                    event_start_date: [
                        {type: 'date', required: true, message: 'Please pick a start time', trigger: 'change'}
                    ]
                }
            };
        },
        methods: {
            submitForm (formName) {
                this.$refs[formName].validate((valid) => {
                    if(valid) {
                        this.loading = true;
                        if(this.$route.params.eventId === undefined) {
                            EventRepository.postCreateEvent(this.eventManagement).then((response) => {
                                this.loading = false;
                                this.$message({
                                    type: 'success',
                                    message: response.data.data.message,
                                });
                                return this.$router.push({name: 'EventManagement'})
                            });
                        } else {
                            EventRepository.putEditEvent(this.$route.params.eventId, this.eventManagement).then((response) => {
                                this.loading = false;
                                this.$message({
                                    type: 'success',
                                    message: response.data.data.message,
                                });
                                return this.$router.push({name: 'EventManagement'})
                            });
                        }
                    } else {
                        return false;
                    }
                });
            },
            getTypeEvent () {
                EventRepository.getTypeEvent().then((response) => {
                    this.typeEvents = response.data.data;
                });
            },
            getBranches () {
                branch.getBranches().then((response) => {
                    this.branches = response.data.data;
                });
            },
            cancel () {
                return this.$router.push({name: 'EventManagement'})
            },
            fetchEvent () {
                EventRepository.findEvent({eventId: this.$route.params.eventId}).then((response) => {
                    this.eventManagement = helpers.convertStringToTime(response.data.data, ['event_end_date', 'event_start_date']);
                });
            }
        },
        mounted () {
            this.getTypeEvent();
            this.getBranches();
            if(this.$route.params.eventId !== undefined) {
                this.pageTitle = 'Edit Event'
                this.fetchEvent();
            }
        }
    }
</script>
