<template>
    <div>
        <div class="row">
            <div class="col-sm-4 col-3">
                <h4 class="page-title">{{this.pageTitle}}</h4>
            </div>
        </div>
        <div class="box-content box box-primary">
            <div class="box-body box-form-content">
                <el-form :model="school" :rules="rules" ref="schoolForm" v-loading.body="loading"
                         label-width="120px"
                         label-position="top"
                         class="demo-ruleForm">
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Name School" prop="title">
                                <el-input v-model="school.title"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12">
                            <el-form-item label="Status" prop="status">
                                <el-radio-group v-model="school.status">
                                    <el-radio :label="0" border>Hidden</el-radio>
                                    <el-radio :label="1" border>Show</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </div>
                    </div>
                    <el-form-item>
                        <el-button type="success" round @click="submitForm('schoolForm')">Save</el-button>
                        <el-button type="info" round @click="cancel()">Cancel</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>
<script>
    import {RepositoryFactory} from '../../../repositories/RepositoryFactory'
    import helpers from '../../../helpers'

    const schoolRe = RepositoryFactory.get('school');
    export default {
        data () {
            return {
                school: {
                    title: '',
                    status: 0
                },
                loading: false,
                pageTitle: 'Create School',
                rules: {
                    title: [
                        {required: true, message: 'Please enter name school', trigger: 'blur'}
                    ]
                }
            };
        },
        methods: {
            submitForm (formName) {
                this.$refs[formName].validate((valid) => {
                    if(valid) {
                        this.loading = true;
                        if(this.$route.params.schoolId === undefined) {
                            schoolRe.postCreateSchool(this.school).then((response) => {
                                this.loading = false;
                                this.$message({
                                    type: 'success',
                                    message: response.data.data.message,
                                });
                                return this.$router.push({name: 'School'})
                            });
                        } else {
                            schoolRe.putEditSchool(this.$route.params.schoolId, this.school).then((response) => {
                                this.loading = false;
                                this.$message({
                                    type: 'success',
                                    message: response.data.data.message,
                                });
                                return this.$router.push({name: 'School'})
                            });
                        }
                    } else {
                        return false;
                    }
                });
            },
            cancel () {
                return this.$router.push({name: 'School'})
            },
            fetchSchool () {
                schoolRe.findSchool({schoolId: this.$route.params.schoolId}).then((response) => {
                    this.school = response.data.data;
                });
            }
        },
        mounted () {
            if(this.$route.params.schoolId !== undefined) {
                this.pageTitle = 'Edit School'
                this.fetchSchool();
            }
        }
    }
</script>
