import axios from 'axios'

const apiGetSchools = "/get-schools"
const apiAllSchools = "/get-all-schools"
const apiPostCreateSchool = "/store-schools"
const apiFindSchool = "/find-school"
const apiUpdateSchool = "/update-school/"
export default {
    postCreateSchool (data) {
        return axios.post(apiPostCreateSchool, data);
    },
    putEditSchool (schoolId, data) {
        return axios.put(apiUpdateSchool + schoolId, data);
    },
    getSchools (data) {
        return axios.get(apiGetSchools, {params: data});
    },
    findSchool (data) {
        return axios.get(apiFindSchool, {params: data});
    },
    getAllSchools () {
        return axios.get(apiAllSchools);
    }
}
