                            <el-form-item label="Title" prop="title">
                                <el-select v-model="student.title" placeholder="Select title">
                                    <el-option
                                            v-for="item in listTitles"
                                            :key="item.key"
                                            :label="item.title"
                                            :value="item.key">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                        <div class="col-md-6">
                            <el-form-item label="First Name" prop="first_name">
                                <el-input v-model="student.first_name" placeholder="Please enter first name"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Last Name">
                                <el-input v-model="student.last_name" placeholder="Please enter last name"></el-input>
                            </el-form-item>
                        </div>
                        <div class="col-md-6">
                            <el-form-item label="Nick Name">
                                <el-input v-model="student.nick_name" placeholder="Please enter nick name"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Birthdate" prop="birthday">
                                <el-date-picker
                                        v-model="student.birthday"
                                        type="date"
                                        placeholder="Pick a day">
                                </el-date-picker>
                            </el-form-item>
                        </div>
                        <div class="col-md-6">
                            <el-form-item label="Address">
                                <el-input v-model="student.address" placeholder="Please enter address"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Email" prop="email">
                                <el-input v-model="student.email" placeholder="Please enter email"></el-input>
                            </el-form-item>
                        </div>
                        <div class="col-md-6">
                            <el-form-item label="Phone" prop="phone">
                                <el-input v-model="student.phone" placeholder="Please enter phone"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="School">
                                <el-select v-model="student.school_id" placeholder="Select school"
                                           @change="changeSchool">
                                    <el-option
                                            v-for="item in schools"
                                            :key="item.id"
                                            :label="item.title"
                                            :value="item.id">
                                    </el-option>
                                    <el-option
                                            key="other"
                                            label="Other"
                                            value="other">
                                    </el-option>

                                </el-select>
                            </el-form-item>
                        </div>
                        <div class="col-md-6">
                            <el-form-item label="Name School New" v-if="student.schoolNew">
                                <el-input v-model="student.schoolNewName"
                                          placeholder="Please enter name school"></el-input>
                            </el-form-item>
                        </div>
                        <div class="col-md-6">
                            <el-form-item label="Note School">
                                <el-input v-model="student.note_school"
                                          placeholder="Please enter note school"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Facebook ID" prop="facebook_id">
                                <el-input v-model="student.facebook_id"
                                          placeholder="Please enter facebook id"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Emergency contact name">
                                <el-input v-model="student.emergency_contact_name"
                                          placeholder="Please enter email"></el-input>
                            </el-form-item>
                        </div>
                        <div class="col-md-6">
                            <el-form-item label="Emergency contact email">
                                <el-input v-model="student.emergency_contact_email"
                                          placeholder="Please enter email"></el-input>
                            </el-form-item>
                        </div>
                        <div class="col-md-6">
                            <el-form-item label="Emergency contact phone">
                                <el-input v-model="student.emergency_contact_phone"
                                          placeholder="Please enter phone"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <hr>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Level" prop="level_id">
                                <el-select v-model="student.level_id" placeholder="Select level">
                                    <el-option
                                            v-for="item in levels"
                                            :key="item.id"
                                            :label="item.title"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                        <div class="col-md-6">
                            <el-form-item label="Personal year of entry">
                                <el-date-picker
                                        v-model="student.personal_year_of_entry"
                                        type="month"
                                        placeholder="Pick a month">
                                </el-date-picker>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="How you hear about us">
                                <el-select v-model="student.selfknow_id" placeholder="Select how you hear about us">
                                    <el-option
                                            v-for="item in selfKnows"
                                            :key="item.id"
                                            :label="item.title"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                        <div class="col-md-6">
                            <el-form-item label="Subject Area" prop="subjectarea_id">
                                <el-select v-model="student.subjectarea_id" placeholder="Select Subject Area">
                                    <el-option
                                            v-for="item in subjects"
                                            :key="item.id"
                                            :label="item.title"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                    <hr>
                    <div class="row">
                        <div class="col-md-6">
                            <el-form-item label="Branch" prop="branch_id">
                                <el-select v-model="student.branch_id" placeholder="Select branch">
                                    <el-option
                                            v-for="item in branches"
                                            :key="item.id"
                                            :label="item.title"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                    <el-form-item>
                        <el-button type="success" round @click="submitForm('partnerForm')">Save</el-button>
                        <el-button type="info" round @click="cancel()">Cancel</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>
<script>
    import {RepositoryFactory} from '../../../repositories/RepositoryFactory'

    const studentRe = RepositoryFactory.get('student');
    const branch = RepositoryFactory.get('branch');
    export default {
        data () {
            return {
                student: {
                    title: '',
                    first_name: '',
                    last_name: '',
                    nick_name: '',
                    email: '',
                    birthday: '',
                    phone: '',
                    address: '',
                    facebook_id: '',
                    emergency_contact_name: '',
                    emergency_contact_email: '',
                    emergency_contact_phone: '',
                    school_id: '',
                    schoolNew: false,
                    schoolNewName: '',
                    note_school: '',
                    level_id: '',
                    personal_year_of_entry: '',
                    selfknow_id: '',
                    subjectarea_id: '',
                    note: '',
                    primary_counsellor_id: '',
                    secondary_counsellor_id: '',
                    admin_counsellor_id: '',
                    country_destination_id: '',
                    university_id: '',
                    branch_id: '',
                    partner_id: '',
                    staff_create_id: '',
                    priority: '',
                    maintenance: '',
                    maintenance_note: ''
                },
                branches: [],
                schools: [],
                selfKnows: [],
                subjects: [],
                levels: [],
                listTitles: [
                    {key: 'Dr', title: 'Dr'},
                    {key: 'Mr', title: 'Mr'},
                    {key: 'Miss', title: 'Miss'},
                    {key: 'Mrs', title: 'Mrs'},
                    {key: 'Ms', title: 'Ms'}
                ],
                listStaffs: [],
                loading: false,
                pageTitle: 'Create Student',
                rules: {
                    title: [
                        {required: true, message: 'Please choose title', trigger: 'change'}
                    ],
                    first_name: [
                        {required: true, message: 'Please enter first name', trigger: 'blur'}
                    ],
                    birthday: [
                        {required: true, message: 'Please pick a day', trigger: 'change'}
                    ],
                    email: [
                        {required: true, message: 'Please enter email', trigger: 'blur'},
                        {type: 'email', message: 'Please enter correct email address', trigger: ['blur', 'change']},
                    ],
                    phone: [
                        {required: true, message: 'Please enter phone', trigger: 'blur'},
                    ],
                    level_id: [
                        {required: true, message: 'Please choose level', trigger: 'change'}
                    ],
                    subjectarea_id: [
                        {required: true, message: 'Please choose subject area', trigger: 'change'}
                    ],
                    branch_id: [
                        {required: true, message: 'Please choose branch', trigger: 'change'}
                    ]
                }
            };
        },
        methods: {
            submitForm (formName) {
                this.$refs[formName].validate((valid) => {
                    if(valid) {
                        this.loading = true;
                        if(this.$route.params.partnerId === undefined) {
                            partnerRe.postCreatePartner(this.partner).then((response) => {
                                this.loading = false;
                                this.$message({
                                    type: 'success',
                                    message: response.data.data.message,
                                });
                                return this.$router.push({name: 'Partner'})
                            });
                        } else {
                            partnerRe.putEditPartner(this.$route.params.partnerId, this.partner).then((response) => {
                                this.loading = false;
                                this.$message({
                                    type: 'success',
                                    message: response.data.data.message,
                                });
                                return this.$router.push({name: 'Partner'})
                            });
                        }
                    } else {
                        return false;
                    }
                });
            },
            changeSchool (value) {
                if(value == 'other') {
                    this.student.schoolNew = true;
                } else {
                    this.student.schoolNew = false;
                    this.student.schoolNewName = '';
                }
            },
            cancel () {
                return this.$router.push({name: 'Partner'})
            },
            fetchPartner () {
                partnerRe.findPartner({partnerId: this.$route.params.partnerId}).then((response) => {
                    this.partner = response.data.data;
                });
            },
            getBranches () {
                branch.getBranches().then((response) => {
                    this.branches = response.data.data;
                    this.student.branch_id = response.data.data[0]['id'];
                });
            },
        },
        mounted () {
            this.getBranches();
            if(this.$route.params.partnerId !== undefined) {
                this.pageTitle = 'Edit Student'
                this.fetchPartner();
            }
        }
    }
</script>
