import axios from 'axios'

const apiGetSelfKnows = "/get-self-knows"
const apiAllSelfKnows = "/get-all-self-knows"
const apiPostCreateSelfKnow = "/store-selfknow"
const apiFindSelfKnow = "/find-selfknow"
const apiUpdateSelfKnow = "/update-selfknow/"
export default {
    postCreateSelfKnow (data) {
        return axios.post(apiPostCreateSelfKnow, data);
    },
    putEditSelfKnow (selfKnowId, data) {
        return axios.put(apiUpdateSelfKnow + selfKnowId, data);
    },
    getSelfKnows (data) {
        return axios.get(apiGetSelfKnows, {params: data});
    },
    findSelfKnow (data) {
        return axios.get(apiFindSelfKnow, {params: data});
    },
    GetAllSelfKnows () {
        return axios.get(apiAllSelfKnows);
    }
}
