<template>
    <div>
        <div class="row">
            <div class="col-sm-4 col-3">
                <h4 class="page-title">Lead Conversion</h4>
            </div>
            <div class="col-sm-8 col-9 text-right m-b-20">
                <router-link v-show="checkPermission(['leadconversion.leadconversions.create'])" to="/lead-conversion/create"
                             class="btn btn-primary float-right btn-rounded">
                    <i class="fa fa-plus"></i>
                    Add Lead Conversion
                </router-link>
            </div>
        </div>
        <div class="box-content box box-primary">
            <div class="box-body box-table-content">
                <el-table
                        :data="leadConversions"
                        stripe
                        style="width: 100%"
                        ref="pageTable"
                        v-loading.body="tableIsLoading"
                        @sort-change="handleSortChange"
                        @selection-change="handleSelectionChange">
                    <el-table-column
                            prop="id"
                            label="ID"
                            width="50">
                    </el-table-column>
                    <el-table-column
                            prop="title"
                            label="Title"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="first_name"
                            label="First Name"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="last_name"
                            label="Last Name">
                    </el-table-column>
                    <el-table-column
                            prop="email"
                            label="Email">
                    </el-table-column>
                    <el-table-column
                            prop="phone"
                            label="Phone">
                    </el-table-column>
                    <el-table-column
                            prop="score"
                            label="Score">
                    </el-table-column>
                    <el-table-column
                            prop="score"
                            label="Score">
                    </el-table-column>
                </el-table>
                <div class="pagination-wrap">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page.sync="meta.current_page"
                            :page-sizes="[10, 20, 50, 100]"
                            :page-size="parseInt(meta.per_page)"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="meta.total">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {RepositoryFactory} from '../../../repositories/RepositoryFactory'
    import _ from 'lodash';
    const leadRepository = RepositoryFactory.get('leadConversions');
    export default {
        data () {
            return {
                leadConversions: [],
                meta: {
                    current_page: 1,
                    per_page: 10,
                },
                order_meta: {
                    order_by: '',
                    order: '',
                },
                links: {},
                searchQuery: '',
                tableIsLoading: false,
                showExtraButtons: false,
                selectedPages: {},
            };
        },
        watch: {
            selectedPages () {
                this.showExtraButtons = this.selectedPages.length >= 1;
            },
        },
        methods: {
            queryServer (customProperties) {
                const properties = {
                    page: this.meta.current_page,
                    per_page: this.meta.per_page,
                    order_by: this.order_meta.order_by,
                    order: this.order_meta.order,
                    search: this.searchQuery,
                };
                this.tableIsLoading = true;
                leadRepository.getLeadConversion(_.merge(properties, customProperties)).then((response) => {
                    this.tableIsLoading = false;
                    this.leadConversions = response.data.data;
                    this.meta = response.data.meta;
                    this.links = response.data.links;

                    this.order_meta.order_by = properties.order_by;
                    this.order_meta.order = properties.order;
                }).catch(error => {
                    this.tableIsLoading = false;
                });
            },
            getLeadConversion () {
                this.tableIsLoading = true;
                this.queryServer();
            },
            handleSizeChange (event) {
                console.log(`per page :${event}`);
                this.tableIsLoading = true;
                this.queryServer({per_page: event});
            },
            handleCurrentChange (event) {
                console.log(`current page :${event}`);
                this.tableIsLoading = true;
                this.queryServer({page: event});
            },
            handleSortChange (event) {
                console.log('sorting', event);
                this.tableIsLoading = true;
                this.queryServer({
                    order_by: event.prop,
                    order: event.order
                });
            },
            performSearch: _.debounce(function (query) {
                console.log(`searching:${query.target.value}`);
                this.tableIsLoading = true;
                this.queryServer({search: query.target.value});
            }, 300),
            handleSelectionChange (selectedPages) {
                this.selectedPages = selectedPages;
            }
        },
        mounted () {
            this.getLeadConversion();
        }
    }
</script>
