import axios from 'axios'

const apiGetTypeEvent = "/get-type-event"
const apiGetEvents = "/get-events"
const apiAllEvent = "/get-all-event"
const apiPostCreateEvent = "/post-create-event"
const apiFindEvent = "/find-event"
const apiUpdateEvent = "/update-event"
export default {
    getTypeEvent () {
        return axios.get(apiGetTypeEvent);
    },
    postCreateEvent (data) {
        return axios.post(apiPostCreateEvent, data);
    },
    putEditEvent (eventId, data) {
        return axios.put(apiUpdateEvent + '/' + eventId, data);
    },
    getEvents (data) {
        return axios.get(apiGetEvents, {params: data});
    },
    findEvent (data) {
        return axios.get(apiFindEvent, {params: data});
    },
    getAllEvent () {
        return axios.get(apiAllEvent);
    }
}
