import axios from 'axios'

const apiGetPartners = "/get-partners"
const apiAllPartners = "/get-all-partners"
const apiPostCreatePartner = "/store-partner"
const apiFindPartner = "/find-partner"
const apiUpdatePartner = "/update-partner/"
export default {
    postCreatePartner (data) {
        return axios.post(apiPostCreatePartner, data);
    },
    putEditPartner (PartnerId, data) {
        return axios.put(apiUpdatePartner + PartnerId, data);
    },
    getPartners (data) {
        return axios.get(apiGetPartners, {params: data});
    },
    findPartner (data) {
        return axios.get(apiFindPartner, {params: data});
    },
    GetAllPartners () {
        return axios.get(apiAllPartners);
    }
}
