<script>
    import {RepositoryFactory} from '../../../repositories/RepositoryFactory'

    const EventRepository = RepositoryFactory.get('EventRepository');
    const branch = RepositoryFactory.get('branch');
    const Lead = RepositoryFactory.get('leadConversions');
    export default {
        data () {
            return {
                leadConversion: {
                    event_id: '',
                    country_destination_id: '',
                    university_id: '',
                    level_id: '',
                    branch_id: '',
                    subjectarea_id: '',
                    title: '',
                    first_name: '',
                    last_name: '',
                    email: '',
                    phone: '',
                    post_code: '',
                    note: '',
                    subject_bachelor: '',
                    university_bachelor: '',
                    gpa_bachelor: '',
                    year_finished_bachelor: '',
                    subject_degree: '',
                    university_degree: '',
                    gpa_degree: '',
                    year_finished_degree: '',
                },
                pageTitle: 'LEAD CONVERSION :: ADD',
                ruleForm: {
                    event_id: [
                        {required: true, message: 'The event is field required'}
                    ],
                    branch_id: [
                        {required: true, message: 'The branch is field required'}
                    ],
                    subjectarea_id: [
                        {required: true, message: 'The subject area is field required'}
                    ],
                    level_id: [
                        {required: true, message: 'The level is field required'}
                    ],
                    first_name: [
                        {required: true, message: 'The first name is field required'}
                    ],
                    email: [
                        {required: true, message: 'The email is field required'},
                        {type: 'email', message: 'The email is not a valid email'}
                    ]
                },
                events: [],
                branches: [],
                country_destinations: [],
                universities: [],
                subjectareas: [],
                levels: [],
                titles: {Dr: 'Dr', Mr: 'Mr', Miss: 'Miss', Mrs: 'Mrs'}
            };
        },
        methods: {
            submitForm (formName) {
                this.$refs[formName].validate((valid) => {
                    if(valid) {
                        Lead.postCreateLead(this.leadConversion).then((response) => {
                            if(typeof response.data.data.message !== undefined) {
                                this.$message.success(response.data.data.message)
                            }
                            this.$router.push({name: 'LeadConversion'})
                        });
                    } else {
                        return false;
                    }
                });
            },
            fetchDataGobal () {
                branch.getBranches().then((response) => {
                    this.branches = response.data.data
                });
                EventRepository.getAllEvent().then((response) => {
                    this.events = response.data.data
                });
                Lead.getCountries().then((response) => {
                    this.country_destinations = response.data.data
                });
                Lead.getLevel().then((response) => {
                    this.levels = response.data.data
                });
                Lead.getSubjectArea().then((response) => {
                    this.subjectareas = response.data.data
                });
            },
            changeCountry (data) {
                Lead.getUniversityByCountries({country_id: data}).then((response) => {
                    this.universities = response.data.data
                });
            }
        },
        mounted () {
            this.fetchDataGobal();
        }
    }
</script>
